.expense-item {
  display: flex;
  justify-content: flex-start;
  gap: 3rem;
  align-items: center;
  /* box-shadow: 0 2px 5px rgba(0, 0, 0, 0.25); */
  padding: 0.6rem;
  padding-left: 1.5rem;
  margin: 0.5rem 0;
  background-color:lightcoral;
  color: pink;
  width: 100%;

}

.expense-item__description {
  display: flex;
  flex-grow: 1;
  /* flex-direction: column; */
  justify-content: space-between;
  gap: 1rem;
  align-items: center;
  /* flex-flow: column-reverse; */
}

.expense-item h2 {
  color: white;
  font-size: 1rem;
  flex: 1;
  margin: 0 1rem;
}

.expense-item__description h2 {
  font-size: 1.25rem;
  text-align: left;
}

.expense-item__price {
  font-size: 1.25rem;
  padding: 0.5rem 1.5rem;
  color: white;
}

@media (max-width: 600px) {
  .expense-item__description {
    display: flex;
    flex-direction: column;
    align-items:flex-end;
    gap: 0.2rem;
  }

  .expense-item__description h2 {
    text-align: right;
  }

  .expense-item__price {
    font-size: 1.25rem;
    padding: 0rem 1.0rem;
    color: white;
  }
}
